@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.main {
  text-align: center;
}
@media screen and (min-width: 768px) {
  .main {
    max-width: 74.375rem;
    width: 100%;
  }
}
.btn {
  display: inline-block;
  max-width: 20.9375rem;
  width: 100%;
  background-color: #333;
  color: #fff;
  padding-top: 0.625rem;
  padding-bottom: 0.6875rem;
  border: 1px solid #fff;
  overflow: hidden;
  position: relative;
}
.btn::before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
  transform: translate(-100%, 0);
  transition: 0.3s;
}

.btn:hover::before {
  transform: translate(0, 0);
  transition: 0.3s;
}

.btn:hover span {
  position: relative;
  z-index: 10;
  color: #333;
  transition: 0.3s;
}

.overview__btn {
  margin-bottom: 1.3125rem;
}
.overview__contents--inner {
  padding: 0 1.25rem;
  background-color: #333;
}
@media screen and (min-width: 768px) {
  .overview__contents--inner {
    padding: 7.4375rem 50% 7.4375rem 5.625rem;
  }
}
.overview__img {
  display: inline-block;
  max-width: 23.4375rem;
  width: 100%;
  aspect-ratio: 4/3;
  overflow: hidden;
  position: relative;
}
@media screen and (min-width: 768px) {
  .overview__img {
    max-width: 34.8125rem;
    top: 50%;
    transform: translate(30%, -50%);
    width: 100%;
    position: absolute;
  }
}
.overview__img::before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  background-image: url(/images/office@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}
.overview__inner {
  padding: 4.5rem 0 0 0;
}
@media screen and (min-width: 768px) {
  .overview__inner {
    position: relative;
  }
}
.overview__subTitle {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.45;
  padding-bottom: 1.5rem;
  color: #fff;
}
@media screen and (min-width: 768px) {
  .overview__subTitle {
    padding-bottom: 3rem;
  }
}
.overview__text {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.125rem;
  line-height: 1.4375;
  text-align: left;
  padding: 0 0 1rem 0;
  color: #fff;
}
.overview__title {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #fff;
  position: relative;
  padding: 0 0 3rem 0;
}
.overview__title::before {
  content: "Overview";
  color: #f9f871;
  font-family: "Noto Serif JP", serif;
  font-weight: 400;
  font-size: 3.125rem;
  opacity: 0.27;
  display: inline-block;
  position: absolute;
  top: -64%;
  left: 50%;
  transform: translate(-50%, 0%);
}
.overview {
  display: inline-block;
  max-width: 23.4375rem;
  width: 100%;
  height: 100%;
  background-color: #111;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .overview {
    max-width: 56rem;
    width: 100%;
  }
}